body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background-color: rgb(230, 234, 230);
  font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-size: 15px;
  padding-top: 1px;
}

.header{
  text-align: center;
  /* position:; */
}

li {
  list-style-type: none;
}

.github{
  height: 50px;
  width: 50px;
  float:left;
  margin-top: 10px;

}

.linkedin{

  height: 50px;
  width: 50px;
  float:left;
  margin-left: 10px;
  margin-top: 10px;
}

h1{
  color: black;
}

.logoImage{
  border-radius: 70%;
  width: 40%;
  display:flex;
  position: relative;
  top:100px;
  left: 200px;
}

.about {
  display: flex;
  text-align: justify;  
  padding: 8px;
  text-align:justify;
  width: 700px; 
  height: auto;
  overflow: auto;

}


.Footer{
  background-color: #FADCD9;
  font-size: 10px;
  position: fixed;
  text-align: left;
  bottom: 0%;
  width: 80%
}

/* Split the screen in half */
.split {
  height: 100%;
  width: 50%;
  position: fixed;
  /* padding-top: 3px; */
}

/* Control the left side */
.left {
  left: 0;
  top: 90px;
  background-color: #F9F1F0;
  float: left;
  height: auto;

}

.right {
  right: 0;
  top: 90px;
  background-color: #FADCD9;
}

a:link{
  text-decoration: none;
}
a:visited{
  text-decoration: none;
}
a:hover{
  text-decoration: none;
}
a:active{
  text-decoration: none;
}
